.container {
  & > h1 {
    font-size: 2rem;
    color: var(--text);
    font-weight: 600;
    margin-bottom: 16px;
  }
  margin-bottom: 96px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 16px;
}

.opContainer {
  position: relative;
  border-radius: 5px;
  overflow: hidden;

  & > img {
    width: 100%;
    height: 100%;
  }

  &:hover {
    .backdrop {
      opacity: 1;
    }
  }
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    transparent 40%,
    rgba(0, 0, 0, 0.6) 90%
  );
  padding: 16px;
  opacity: 0;
  transition: opacity 0.3s;

  display: flex;
  align-items: flex-end;

  & > span {
    font-weight: 600;
    color: var(--white);
  }
}

@media screen and (max-width: 600px) {
  .grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }
  .container {
    & > h1 {
      font-size: 1.5rem;
    }
  }
}
