@use '../styles/shared';

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 160px;
}

.imgSection {
  position: relative;
  opacity: 0;
  transform: translateX(80px);
  transition: all ease-out 0.5s;

  & > img {
    height: 360px;
    width: auto;
    @extend %picShadow;
    border-radius: 10px;
  }

  & > svg {
    position: absolute;
    z-index: -1;
    bottom: -80px;
    right: -10px;
    height: auto;
    width: 240px;
  }
}

.proDetails {
  width: 400px;
  margin-right: 64px;
  transform: translateX(-80px);
  opacity: 0;
  transition: all ease-out 0.5s;

  & > h1 {
    font-size: 2rem;
    color: var(--text);
    font-weight: 600;
  }

  & > h2 {
    font-size: 1.2rem;
    font-weight: 400;
    color: var(--text-light-1);
    margin-bottom: 16px;
  }

  & > p {
    margin-bottom: 32px;
  }
}

.appear {
  opacity: 1;
  transform: translateX(0px);
}

@media screen and (max-width: 600px) {
  .container {
    flex-direction: column-reverse;
  }
  .imgSection {
    margin-bottom: 96px;
  }
  .proDetails {
    width: 100%;
    margin-right: 0;
    & > h1 {
      font-size: 1.5rem;
    }

    & > h2 {
      font-size: 1rem;
    }

    & > p {
      font-size: 0.8rem;
    }
  }
}
